import React, { useState, useEffect } from 'react';
import { motion } from 'framer-motion';
import '../styles/MotivationalQuote.css';

interface MotivationalQuoteProps {
  onClose: () => void;
}

// База цитат для мотивації
const quotes = [
  {
    text: "Успіх - це здатність рухатися від однієї невдачі до іншої, не втрачаючи ентузіазму.",
    author: "Вінстон Черчилль"
  },
  {
    text: "Єдиний спосіб робити велику роботу - любити те, що ви робите.",
    author: "Стів Джобс"
  },
  {
    text: "Не порівнюйте себе з іншими. Порівнюйте себе з тим, ким ви були вчора.",
    author: "Джордан Пітерсон"
  },
  {
    text: "Перший крок до успіху - це зробити перший крок.",
    author: "Марк Твен"
  },
  {
    text: "Я не зазнав поразки. Я просто знайшов 10 000 способів, які не працюють.",
    author: "Томас Едісон"
  },
  {
    text: "Ваш час обмежений, тому не витрачайте його, живучи чужим життям.",
    author: "Стів Джобс"
  },
  {
    text: "Дійте або будете підпорядковані обставинам.",
    author: "Леонардо да Вінчі"
  },
  {
    text: "Життя - це те, що відбувається з вами, поки ви будуєте інші плани.",
    author: "Джон Леннон"
  },
  {
    text: "Коли ти піднімаєшся на гору, ти розумієш, що існує ще багато гір, які треба підкорити.",
    author: "Нельсон Мандела"
  },
  {
    text: "Найбільший ризик – це не ризикувати.",
    author: "Марк Цукерберг"
  }
];

const MotivationalQuote: React.FC<MotivationalQuoteProps> = ({ onClose }) => {
  const [quote, setQuote] = useState({ text: '', author: '' });
  
  useEffect(() => {
    // Вибір випадкової цитати
    const randomIndex = Math.floor(Math.random() * quotes.length);
    setQuote(quotes[randomIndex]);
  }, []);
  
  return (
    <motion.div 
      className="motivational-quote"
      initial={{ opacity: 0, scale: 0.8 }}
      animate={{ opacity: 1, scale: 1 }}
      exit={{ opacity: 0, scale: 0.8 }}
    >
      <div className="quote-content">
        <div className="quote-icon">❝</div>
        <p className="quote-text">{quote.text}</p>
        <p className="quote-author">— {quote.author}</p>
      </div>
      <button className="close-quote-btn" onClick={onClose}>Закрити</button>
    </motion.div>
  );
};

export default MotivationalQuote; 