import React, { useState, useEffect } from 'react';
import { motion } from 'framer-motion';
import storageService, { UserProfile, availableColorSchemes } from '../services/storageService';
import { useNavigate } from 'react-router-dom';
import BottomNavbar from './BottomNavbar';
import '../styles/ProfilePage.css';

const DEFAULT_AVATARS = [
  'https://www.gravatar.com/avatar/00000000000000000000000000000000?d=mp&f=y',
  'https://www.gravatar.com/avatar/11111111111111111111111111111111?d=identicon&f=y',
  'https://www.gravatar.com/avatar/22222222222222222222222222222222?d=monsterid&f=y',
  'https://www.gravatar.com/avatar/33333333333333333333333333333333?d=wavatar&f=y',
  'https://www.gravatar.com/avatar/44444444444444444444444444444444?d=retro&f=y',
  'https://www.gravatar.com/avatar/55555555555555555555555555555555?d=robohash&f=y',
];

interface ProfilePageProps {
  theme: 'light' | 'dark';
  onThemeChange: (theme: 'light' | 'dark') => void;
  colorScheme: string;
  onColorSchemeChange: (scheme: string) => void;
}

const ProfilePage: React.FC<ProfilePageProps> = ({ theme, onThemeChange, colorScheme, onColorSchemeChange }) => {
  const [profile, setProfile] = useState<UserProfile>(() => storageService.getUserProfile());
  const [avatarMenuOpen, setAvatarMenuOpen] = useState(false);
  const [customAvatarUrl, setCustomAvatarUrl] = useState('');
  const [isEditing, setIsEditing] = useState(false);
  const [editedProfile, setEditedProfile] = useState<UserProfile>(profile);
  const [avatarError, setAvatarError] = useState(false);
  const [isTelegramModalOpen, setIsTelegramModalOpen] = useState(false);
  const navigate = useNavigate();

  useEffect(() => {
    // Save profile whenever it changes
    storageService.saveUserProfile(profile);
  }, [profile]);

  const handleAvatarSelect = (avatar: string) => {
    setProfile({...profile, avatar});
    setAvatarMenuOpen(false);
  };

  const handleCustomAvatarSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    if (customAvatarUrl.trim()) {
      setProfile({...profile, avatar: customAvatarUrl});
      setAvatarMenuOpen(false);
      setCustomAvatarUrl('');
    }
  };

  const handleEditToggle = () => {
    if (isEditing) {
      // Зберігаємо зміни
      if (storageService.isValidImageUrl(editedProfile.avatar)) {
        storageService.saveUserProfile(editedProfile);
        setProfile(editedProfile);
        setIsEditing(false);
        setAvatarError(false);
      } else {
        setAvatarError(true);
      }
    } else {
      // Починаємо редагування
      setEditedProfile({ ...profile });
      setIsEditing(true);
    }
  };

  const handleCancel = () => {
    setIsEditing(false);
    setAvatarError(false);
  };

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    setEditedProfile(prev => ({ ...prev, [name]: value }));

    if (name === 'avatar') {
      setAvatarError(false);
    }
  };

  const handleThemeToggle = () => {
    const newTheme = theme === 'light' ? 'dark' : 'light';
    onThemeChange(newTheme);
  };

  // Функції для роботи з Telegram підключенням
  const handleTelegramConnect = () => {
    setIsTelegramModalOpen(true);
  };

  const handleCloseTelegramModal = () => {
    setIsTelegramModalOpen(false);
  };

  const generateTelegramCode = () => {
    const code = Math.floor(100000 + Math.random() * 900000).toString();
    const expiresDate = new Date();
    expiresDate.setMinutes(expiresDate.getMinutes() + 10); // Код дійсний 10 хвилин
    
    // Оновлюємо профіль користувача
    const updatedProfile = { 
      ...profile, 
      telegram: { 
        connected: false,
        connectionCode: code,
        codeExpires: expiresDate.toISOString()
      } 
    };
    
    storageService.saveUserProfile(updatedProfile);
    setProfile(updatedProfile);
  };

  return (
    <motion.div 
      className="profile-page container"
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      exit={{ opacity: 0 }}
    >
      <div className="profile-header">
        <h1>Профіль</h1>
        <button 
          className="edit-profile-btn" 
          onClick={handleEditToggle}
        >
          {isEditing ? 'Зберегти' : 'Редагувати'}
        </button>
        {isEditing && (
          <button 
            className="cancel-profile-btn" 
            onClick={handleCancel}
          >
            Скасувати
          </button>
        )}
      </div>

      <div className="profile-content">
        <div className="profile-avatar-container">
          <div 
            className="avatar-container"
            onClick={() => setAvatarMenuOpen(!avatarMenuOpen)}
          >
            <img 
              src={profile.avatar} 
              alt="Avatar" 
              className="profile-avatar"
              onError={(e) => {
                const target = e.target as HTMLImageElement;
                target.src = "https://www.gravatar.com/avatar/00000000000000000000000000000000?d=mp&f=y";
              }}
            />
            <div className="avatar-edit-badge">
              <svg width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
                <path d="M11 4H4a2 2 0 0 0-2 2v14a2 2 0 0 0 2 2h14a2 2 0 0 0 2-2v-7"></path>
                <path d="M18.5 2.5a2.121 2.121 0 0 1 3 3L12 15l-4 1 1-4 9.5-9.5z"></path>
              </svg>
            </div>
          </div>

          {avatarMenuOpen && (
            <motion.div 
              className="avatar-menu"
              initial={{ scale: 0.8, opacity: 0 }}
              animate={{ scale: 1, opacity: 1 }}
              exit={{ scale: 0.8, opacity: 0 }}
              transition={{ type: "spring", stiffness: 300, damping: 20 }}
            >
              <div className="avatar-options">
                {DEFAULT_AVATARS.map((avatar, index) => (
                  <img 
                    key={index}
                    src={avatar}
                    alt={`Avatar option ${index + 1}`}
                    onClick={() => handleAvatarSelect(avatar)}
                    className="avatar-option"
                    onError={(e) => {
                      const target = e.target as HTMLImageElement;
                      target.src = "https://www.gravatar.com/avatar/00000000000000000000000000000000?d=mp&f=y";
                    }}
                  />
                ))}
              </div>
              
              <div className="custom-avatar-section">
                <form onSubmit={handleCustomAvatarSubmit}>
                  <input
                    type="text"
                    value={customAvatarUrl}
                    onChange={(e) => setCustomAvatarUrl(e.target.value)}
                    placeholder="Введіть URL зображення"
                  />
                  <button type="submit">Встановити</button>
                </form>
              </div>
              
              <button 
                className="close-avatar-menu"
                onClick={() => setAvatarMenuOpen(false)}
              >
                Закрити
              </button>
            </motion.div>
          )}
        </div>

        <div className="profile-details">
          {isEditing ? (
            <>
              <div className="input-group">
                <label htmlFor="name-input">Ім'я</label>
                <input 
                  id="name-input"
                  type="text" 
                  name="name" 
                  value={editedProfile.name} 
                  onChange={handleInputChange}
                  placeholder="Ваше ім'я" 
                />
              </div>
              
              <div className="input-group">
                <label htmlFor="email-input">Email</label>
                <input 
                  id="email-input"
                  type="email" 
                  name="email" 
                  value={editedProfile.email} 
                  onChange={handleInputChange}
                  placeholder="Ваш email" 
                />
              </div>
            </>
          ) : (
            <>
              <h2>{profile.name}</h2>
              <p>{profile.email}</p>
              <p>Користувач з: {new Date(profile.createdAt).toLocaleDateString()}</p>
              
              {/* Telegram інтеграція */}
              <div className="telegram-integration">
                <h3>Telegram інтеграція</h3>
                {profile.telegram?.connected ? (
                  <div className="telegram-connected">
                    <p>Підключено до: @{profile.telegram.username}</p>
                    <button 
                      className="disconnect-telegram-btn" 
                      onClick={() => {
                        const updatedProfile = { ...profile, telegram: { connected: false } };
                        storageService.saveUserProfile(updatedProfile);
                        setProfile(updatedProfile);
                      }}
                    >
                      Відключити
                    </button>
                  </div>
                ) : (
                  <button 
                    className="connect-telegram-btn" 
                    onClick={handleTelegramConnect}
                  >
                    Підключити Telegram
                  </button>
                )}
              </div>
            </>
          )}
        </div>
      </div>

      {/* Налаштування теми */}
      <div className="theme-settings">
        <h3>Налаштування теми</h3>
        <div className="theme-toggle">
          <label className="switch">
            <input 
              type="checkbox" 
              checked={theme === 'dark'} 
              onChange={handleThemeToggle} 
            />
            <span className="slider round"></span>
          </label>
          <span>{theme === 'dark' ? 'Темна тема' : 'Світла тема'}</span>
        </div>
      </div>
      
      {/* Селектор кольорових тем */}
      <div className="color-scheme-settings">
        <h3>Кольорова тема</h3>
        <div className="color-schemes">
          {availableColorSchemes.map(scheme => (
            <div 
              key={scheme.id} 
              className={`color-scheme-option ${colorScheme === scheme.id ? 'active' : ''}`}
              onClick={() => onColorSchemeChange(scheme.id)}
            >
              <div className={`color-scheme-preview scheme-${scheme.id}`}>
                <div className="color-tile color-1"></div>
                <div className="color-tile color-2"></div>
                <div className="color-tile color-3"></div>
                <div className="color-tile color-4"></div>
              </div>
              <span className="color-scheme-name">{scheme.name}</span>
            </div>
          ))}
        </div>
      </div>

      {/* Кнопка "Підписка" */}
      <div className="premium-section">
        <h3>Підписка</h3>
        <button 
          className="subscription-btn" 
          onClick={() => navigate('/subscription')}
        >
          Керувати підпискою
        </button>
      </div>

      {/* Модальне вікно для коду Telegram */}
      {isTelegramModalOpen && (
        <div className="modal-overlay">
          <div className="modal telegram-modal">
            <h3>Підключення Telegram</h3>
            {profile.telegram?.connectionCode ? (
              <div className="telegram-code">
                <p>Введіть цей код в нашому боті:</p>
                <div className="code-display">{profile.telegram.connectionCode}</div>
                <p className="code-expires">
                  Код дійсний до: {new Date(profile.telegram.codeExpires || '').toLocaleTimeString()}
                </p>
                <p className="telegram-instructions">
                  1. Відкрийте Telegram і знайдіть нашого бота за адресою @{storageService.getTelegramBotUsername()}<br/>
                  2. Надішліть боту команду /connect<br/>
                  3. Введіть код, що відображається вище
                </p>
              </div>
            ) : (
              <button 
                className="generate-code-btn" 
                onClick={generateTelegramCode}
              >
                Згенерувати код підключення
              </button>
            )}
            <button 
              className="close-modal-btn" 
              onClick={handleCloseTelegramModal}
            >
              Закрити
            </button>
          </div>
        </div>
      )}

      <BottomNavbar activePage="profile" />
    </motion.div>
  );
};

export default ProfilePage; 