import React, { useState } from 'react';
import { motion } from 'framer-motion';
import BottomNavbar from './BottomNavbar';
import '../styles/SubscriptionPage.css';

interface PlanFeature {
  title: string;
  included: boolean;
}

interface SubscriptionPlan {
  id: string;
  name: string;
  price: string;
  duration: string;
  features: PlanFeature[];
  popular?: boolean;
}

const subscriptionPlans: SubscriptionPlan[] = [
  {
    id: 'free',
    name: 'Безкоштовний',
    price: '0₴',
    duration: 'назавжди',
    features: [
      { title: 'До 20 завдань', included: true },
      { title: 'Базові категорії', included: true },
      { title: 'Перегляд статистики', included: true },
      { title: 'Теги для завдань', included: false },
      { title: 'Нагадування через Telegram', included: false },
      { title: 'Тематичне оформлення', included: false },
      { title: 'Пріоритетна підтримка', included: false },
    ]
  },
  {
    id: 'premium',
    name: 'Преміум',
    price: '99₴',
    duration: 'на місяць',
    features: [
      { title: 'Необмежена кількість завдань', included: true },
      { title: 'Всі категорії', included: true },
      { title: 'Розширена статистика', included: true },
      { title: 'Теги для завдань', included: true },
      { title: 'Нагадування через Telegram', included: true },
      { title: 'Тематичне оформлення', included: true },
      { title: 'Пріоритетна підтримка', included: false },
    ],
    popular: true,
  },
  {
    id: 'pro',
    name: 'Про',
    price: '199₴',
    duration: 'на місяць',
    features: [
      { title: 'Все з Преміум', included: true },
      { title: 'Інтеграція з календарем', included: true },
      { title: 'Командна робота', included: true },
      { title: 'API доступ', included: true },
      { title: 'Повний експорт даних', included: true },
      { title: 'Особистий консультант', included: true },
      { title: 'Пріоритетна підтримка', included: true },
    ]
  }
];

const SubscriptionPage: React.FC = () => {
  const [selectedPlan, setSelectedPlan] = useState<string>('free');

  const handlePlanSelect = (planId: string) => {
    setSelectedPlan(planId);
  };

  const handleSubscribe = () => {
    // Тут буде логіка оформлення підписки
    alert(`Ви обрали тариф ${selectedPlan}. Ця функція знаходиться в розробці.`);
  };

  return (
    <motion.div 
      className="subscription-page"
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      exit={{ opacity: 0 }}
    >
      <div className="subscription-header">
        <h1>Підписка</h1>
        <p>Оберіть оптимальний тарифний план</p>
      </div>

      <div className="subscription-plans">
        {subscriptionPlans.map((plan) => (
          <motion.div 
            key={plan.id}
            className={`plan-card ${selectedPlan === plan.id ? 'selected' : ''} ${plan.popular ? 'popular' : ''}`}
            whileHover={{ scale: 1.02 }}
            whileTap={{ scale: 0.98 }}
            onClick={() => handlePlanSelect(plan.id)}
          >
            {plan.popular && <div className="popular-badge">Найпопулярніший</div>}
            <h2 className="plan-name">{plan.name}</h2>
            <div className="plan-price">
              <span className="price">{plan.price}</span>
              <span className="duration">{plan.duration}</span>
            </div>
            <ul className="plan-features">
              {plan.features.map((feature, index) => (
                <li key={index} className={feature.included ? 'included' : 'not-included'}>
                  {feature.included ? '✓' : '✗'} {feature.title}
                </li>
              ))}
            </ul>
            <button 
              className={`select-plan-btn ${selectedPlan === plan.id ? 'selected' : ''}`}
              onClick={() => handlePlanSelect(plan.id)}
            >
              {selectedPlan === plan.id ? 'Обрано' : 'Обрати'}
            </button>
          </motion.div>
        ))}
      </div>

      <div className="subscription-actions">
        <button 
          className="subscribe-btn"
          onClick={handleSubscribe}
          disabled={selectedPlan === 'free'}
        >
          {selectedPlan === 'free' ? 'Ви використовуєте безкоштовний тариф' : 'Оформити підписку'}
        </button>
      </div>

      <div className="subscription-info">
        <p>Всі підписки автоматично продовжуються, поки ви їх не скасуєте. Ви можете скасувати підписку в будь-який момент.</p>
      </div>

      <BottomNavbar />
    </motion.div>
  );
};

export default SubscriptionPage; 