import React, { useCallback, useEffect } from 'react';
// Використовуємо require замість import для модуля canvas-confetti
// eslint-disable-next-line @typescript-eslint/no-var-requires
const confetti = require('canvas-confetti').default || require('canvas-confetti');

interface ConfettiProps {
  isActive: boolean;
  onComplete?: () => void;
}

const Confetti: React.FC<ConfettiProps> = ({ isActive, onComplete }) => {
  const fireConfetti = useCallback(() => {
    const duration = 2000;
    const animationEnd = Date.now() + duration;
    const defaults = { startVelocity: 30, spread: 360, ticks: 60, zIndex: 9999 };

    const randomInRange = (min: number, max: number) => {
      return Math.random() * (max - min) + min;
    };

    const interval: NodeJS.Timeout = setInterval(() => {
      const timeLeft = animationEnd - Date.now();

      if (timeLeft <= 0) {
        clearInterval(interval);
        onComplete && onComplete();
        return;
      }

      const particleCount = 50 * (timeLeft / duration);
      
      // Запускаємо конфетті з випадкових місць зверху екрану
      confetti({
        ...defaults,
        particleCount,
        origin: { x: randomInRange(0.1, 0.3), y: Math.random() - 0.2 },
        colors: ['#ff0000', '#00ff00', '#0000ff', '#ffff00', '#ff00ff', '#00ffff'],
      });
      
      confetti({
        ...defaults,
        particleCount,
        origin: { x: randomInRange(0.7, 0.9), y: Math.random() - 0.2 },
        colors: ['#ff0000', '#00ff00', '#0000ff', '#ffff00', '#ff00ff', '#00ffff'],
      });
    }, 250);

    return () => clearInterval(interval);
  }, [onComplete]);

  useEffect(() => {
    if (isActive) {
      const cleanup = fireConfetti();
      return cleanup;
    }
  }, [isActive, fireConfetti]);

  return null; // Цей компонент нічого не рендерить
};

export default Confetti; 