import React from 'react';
import { Link } from 'react-router-dom';
import { motion } from 'framer-motion';

interface BottomNavbarProps {
  activePage?: string;
}

const BottomNavbar: React.FC<BottomNavbarProps> = ({ activePage = 'tasks' }) => {
  return (
    <motion.nav 
      className="bottom-navbar"
      initial={{ y: 100, opacity: 0 }}
      animate={{ y: 0, opacity: 1 }}
      transition={{ delay: 0.3 }}
    >
      <Link to="/app" className={`nav-item ${activePage === 'tasks' ? 'active' : ''}`}>
        <svg width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
          <path d="M9 11H20"></path>
          <path d="M9 16H20"></path>
          <path d="M9 6H20"></path>
          <path d="M4 10.5L5 11.5L7 9.5"></path>
          <path d="M4 5.5L5 6.5L7 4.5"></path>
          <path d="M4 15.5L5 16.5L7 14.5"></path>
        </svg>
        <span>Завдання</span>
      </Link>
      
      <Link to="/analytics" className={`nav-item ${activePage === 'analytics' ? 'active' : ''}`}>
        <svg width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
          <path d="M18 20V10"></path>
          <path d="M12 20V4"></path>
          <path d="M6 20V14"></path>
        </svg>
        <span>Аналітика</span>
      </Link>
      
      <Link to="/profile" className={`nav-item ${activePage === 'profile' ? 'active' : ''}`}>
        <svg width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
          <path d="M20 21v-2a4 4 0 0 0-4-4H8a4 4 0 0 0-4 4v2"></path>
          <circle cx="12" cy="7" r="4"></circle>
        </svg>
        <span>Профіль</span>
      </Link>
    </motion.nav>
  );
};

export default BottomNavbar; 