import React, { useState, useEffect, useCallback } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import storageService, { Task } from '../services/storageService';
import TaskItem from './TaskItem';
import BottomNavbar from './BottomNavbar';
import PremiumFeature from './PremiumFeature';

const TaskApp: React.FC = () => {
  const [tasks, setTasks] = useState<Task[]>([]);
  const [newTask, setNewTask] = useState<Omit<Task, 'id' | 'createdAt'>>({
    title: '',
    description: '',
    status: 'pending',
    priority: 'medium',
    category: 'personal'
  });
  const [filter, setFilter] = useState({
    status: 'all',
    priority: 'all',
    category: 'all',
    tag: ''
  });
  const [darkMode, setDarkMode] = useState(false);
  const [searchTerm, setSearchTerm] = useState('');
  const [showForm, setShowForm] = useState(false);
  const [allTags, setAllTags] = useState<string[]>([]);

  // Load tasks and theme preference from localStorage on startup
  useEffect(() => {
    const savedTasks = storageService.getTasks();
    setTasks(savedTasks);
    
    const themePreference = storageService.getThemePreference();
    setDarkMode(themePreference === 'dark');
    if (themePreference === 'dark') {
      document.body.classList.add('dark-mode');
    }
    
    updateAllTags(savedTasks);
  }, []);

  // Save tasks to localStorage whenever they change
  useEffect(() => {
    storageService.saveTasks(tasks);
  }, [tasks]);

  // Handle form input changes
  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement | HTMLTextAreaElement>) => {
    const { name, value } = e.target;
    setNewTask(prev => ({ ...prev, [name]: value }));
  };

  // Add new task
  const handleAddTask = () => {
    if (newTask.title.trim() === '') return;
    
    const updatedTasks = [...tasks, storageService.createTask(newTask)];
    setTasks(updatedTasks);
    storageService.saveTasks(updatedTasks);
    setNewTask({
      title: '',
      description: '',
      status: 'pending',
      priority: 'medium',
      category: 'personal'
    });
    setShowForm(false);
  };

  // Update task status
  const handleStatusChange = (id: string, status: Task['status']) => {
    const updatedTasks = tasks.map(task => {
      if (task.id === id) {
        const updatedTask = { ...task, status };
        if (status === 'completed' && task.status !== 'completed') {
          updatedTask.completedAt = new Date().toISOString();
        } else if (status !== 'completed') {
          delete updatedTask.completedAt;
        }
        return updatedTask;
      }
      return task;
    });
    
    setTasks(updatedTasks);
    storageService.saveTasks(updatedTasks);
  };

  // Delete task
  const handleDeleteTask = (id: string) => {
    const updatedTasks = tasks.filter(task => task.id !== id);
    setTasks(updatedTasks);
    storageService.saveTasks(updatedTasks);
    
    updateAllTags(updatedTasks);
  };

  // Set reminder
  const handleSetReminder = (id: string, reminderTime: string, enableTelegram: boolean) => {
    const updatedTasks = tasks.map(task => {
      if (task.id === id) {
        return {
          ...task,
          reminderTime: reminderTime || null,
          reminderSet: !!reminderTime,
          reminderSent: false,
          telegramReminderEnabled: enableTelegram
        };
      }
      return task;
    });
    
    setTasks(updatedTasks);
    storageService.saveTasks(updatedTasks);
  };

  // Add tag to task
  const handleAddTag = (id: string, tag: string) => {
    const updatedTasks = storageService.addTagToTask(tasks, id, tag);
    setTasks(updatedTasks);
    storageService.saveTasks(updatedTasks);
    
    updateAllTags(updatedTasks);
  };

  // Remove tag from task
  const handleRemoveTag = (id: string, tag: string) => {
    const updatedTasks = storageService.removeTagFromTask(tasks, id, tag);
    setTasks(updatedTasks);
    storageService.saveTasks(updatedTasks);
    
    updateAllTags(updatedTasks);
  };

  // Filter and search tasks
  const filteredTasks = tasks.filter(task => {
    const statusMatch = filter.status === 'all' || task.status === filter.status;
    const priorityMatch = filter.priority === 'all' || task.priority === filter.priority;
    const categoryMatch = filter.category === 'all' || task.category === filter.category;
    const tagMatch = !filter.tag || (task.tags && task.tags.includes(filter.tag));
    
    const matchesSearch = !searchTerm || 
      task.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
      task.description.toLowerCase().includes(searchTerm.toLowerCase()) ||
      task.category.toLowerCase().includes(searchTerm.toLowerCase());
    
    return statusMatch && priorityMatch && categoryMatch && tagMatch && matchesSearch;
  });

  // Sort tasks: first incomplete, then by priority
  const sortedTasks = [...filteredTasks].sort((a, b) => {
    if (a.status !== 'completed' && b.status === 'completed') return -1;
    if (a.status === 'completed' && b.status !== 'completed') return 1;
    
    const priorityOrder = { high: 0, medium: 1, low: 2 };
    return priorityOrder[a.priority] - priorityOrder[b.priority];
  });

  // Toggle dark mode
  const toggleDarkMode = () => {
    const newDarkMode = !darkMode;
    setDarkMode(newDarkMode);
    document.body.classList.toggle('dark-mode');
    storageService.saveThemePreference(newDarkMode ? 'dark' : 'light');
  };

  // Calculate completion stats
  const completedCount = tasks.filter(task => task.status === 'completed').length;
  const totalCount = tasks.length;
  const completionPercentage = totalCount ? Math.round((completedCount / totalCount) * 100) : 0;

  // Get unique categories for filter dropdown
  const categories = ['all', ...tasks.map(task => task.category).filter((value, index, self) => self.indexOf(value) === index)];

  // Task entry animation
  const taskVariants = {
    hidden: { opacity: 0, y: 20 },
    visible: (i: number) => ({ 
      opacity: 1, 
      y: 0,
      transition: {
        delay: i * 0.05,
        duration: 0.3
      }
    }),
    exit: { opacity: 0, x: -100, transition: { duration: 0.2 } }
  };

  // Function to update all tags
  const updateAllTags = (currentTasks: Task[]) => {
    const tags = storageService.getAllTags(currentTasks);
    setAllTags(tags);
  };

  return (
    <div className={`app ${darkMode ? 'dark' : 'light'}`}>
      <header className="app-header">
        <h1>PlanPilot</h1>
        <button className="theme-toggle" onClick={toggleDarkMode}>
          {darkMode ? '☀️' : '🌙'}
        </button>
      </header>
      
      <main>
        <motion.section 
          className="task-stats"
          initial={{ opacity: 0, y: -20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.4 }}
        >
          <div className="stats-container">
            <div className="stat-item">
              <h4>Всього завдань</h4>
              <div className="stat-value">{totalCount}</div>
            </div>
            <div className="stat-item">
              <h4>Виконано</h4>
              <div className="stat-value">{completedCount}</div>
            </div>
            <div className="stat-item">
              <h4>Прогрес</h4>
              <div className="stat-progress">
                <div className="progress-bar">
                  <div 
                    className="progress-fill" 
                    style={{ width: `${completionPercentage}%` }}
                  ></div>
                </div>
                <span className="progress-text">{completionPercentage}%</span>
              </div>
            </div>
          </div>
        </motion.section>
        
        <motion.section 
          className="task-filters"
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ duration: 0.4, delay: 0.2 }}
        >
          <div className="filters-header">
            <h2>Фільтри та пошук</h2>
            <motion.button 
              className="add-task-btn"
              whileHover={{ scale: 1.05 }}
              whileTap={{ scale: 0.95 }}
              onClick={() => setShowForm(true)}
            >
              {showForm ? 'Скасувати' : 'Нове завдання'}
            </motion.button>
          </div>

          <div className="search-container">
            <input
              type="text"
              placeholder="Пошук завдань..."
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              className="search-input"
            />
          </div>
          <div className="filters">
            <div className="filter-group">
              <label htmlFor="status-filter">Статус</label>
              <select
                id="status-filter"
                value={filter.status}
                onChange={(e) => setFilter(prev => ({ ...prev, status: e.target.value }))}
              >
                <option value="all">Всі</option>
                <option value="pending">Очікує</option>
                <option value="in-progress">В процесі</option>
                <option value="completed">Виконано</option>
              </select>
            </div>
            
            <div className="filter-group">
              <label htmlFor="priority-filter">Пріоритет</label>
              <select
                id="priority-filter"
                value={filter.priority}
                onChange={(e) => setFilter(prev => ({ ...prev, priority: e.target.value }))}
              >
                <option value="all">Всі</option>
                <option value="low">Низький</option>
                <option value="medium">Середній</option>
                <option value="high">Високий</option>
              </select>
            </div>
            
            <div className="filter-group">
              <label htmlFor="category-filter">Категорія</label>
              <select
                id="category-filter"
                value={filter.category}
                onChange={(e) => setFilter(prev => ({ ...prev, category: e.target.value }))}
              >
                {categories.map((category, index) => (
                  <option key={index} value={category}>
                    {category === 'all' ? 'Всі' : category}
                  </option>
                ))}
              </select>
            </div>
            
            {allTags.length > 0 && (
              <div className="filter-group">
                <label htmlFor="tag-filter">Тег</label>
                <select
                  id="tag-filter"
                  value={filter.tag}
                  onChange={(e) => setFilter(prev => ({ ...prev, tag: e.target.value }))}
                >
                  <option value="">Всі</option>
                  {allTags.map(tag => (
                    <option key={tag} value={tag}>{tag}</option>
                  ))}
                </select>
              </div>
            )}
          </div>
        </motion.section>
        
        <AnimatePresence>
          {showForm && (
            <motion.section 
              className="add-task"
              initial={{ opacity: 0, height: 0 }}
              animate={{ opacity: 1, height: 'auto' }}
              exit={{ opacity: 0, height: 0 }}
              transition={{ duration: 0.3 }}
            >
              <h2>Додати нове завдання</h2>
              <form onSubmit={(e) => { e.preventDefault(); handleAddTask(); }}>
                <div className="form-group">
                  <label htmlFor="title">Назва</label>
                  <input
                    type="text"
                    id="title"
                    name="title"
                    value={newTask.title}
                    onChange={handleInputChange}
                    required
                  />
                </div>
                
                <div className="form-group">
                  <label htmlFor="description">Опис</label>
                  <textarea
                    id="description"
                    name="description"
                    value={newTask.description}
                    onChange={handleInputChange}
                  />
                </div>
                
                <div className="form-row">
                  <div className="form-group">
                    <label htmlFor="priority">Пріоритет</label>
                    <select
                      id="priority"
                      name="priority"
                      value={newTask.priority}
                      onChange={handleInputChange}
                    >
                      <option value="low">Низький</option>
                      <option value="medium">Середній</option>
                      <option value="high">Високий</option>
                    </select>
                  </div>
                  
                  <div className="form-group">
                    <label htmlFor="category">Категорія</label>
                    <input
                      type="text"
                      id="category"
                      name="category"
                      value={newTask.category}
                      onChange={handleInputChange}
                      placeholder="напр., особисте, робота, спорт"
                    />
                  </div>
                </div>
                
                <div className="form-actions">
                  <button type="submit" className="btn btn-primary">Додати завдання</button>
                  <button type="button" className="btn btn-secondary" onClick={() => setShowForm(false)}>Скасувати</button>
                </div>
              </form>
            </motion.section>
          )}
        </AnimatePresence>
        
        <motion.section 
          className="task-list"
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ duration: 0.4, delay: 0.3 }}
        >
          <h2>Мої завдання</h2>
          
          {sortedTasks.length > 0 ? (
            <AnimatePresence>
              {sortedTasks.map((task, index) => (
                <TaskItem 
                  key={task.id}
                  task={task}
                  index={index}
                  onStatusChange={handleStatusChange}
                  onDelete={handleDeleteTask}
                  onReminderSet={handleSetReminder}
                  onAddTag={handleAddTag}
                  onRemoveTag={handleRemoveTag}
                />
              ))}
            </AnimatePresence>
          ) : (
            <div className="no-tasks">
              <p>Немає завдань, що відповідають вашим фільтрам.</p>
              {filter.status !== 'all' || filter.priority !== 'all' || filter.category !== 'all' || filter.tag || searchTerm ? (
                <button 
                  className="btn btn-secondary"
                  onClick={() => {
                    setFilter({ status: 'all', priority: 'all', category: 'all', tag: '' });
                    setSearchTerm('');
                  }}
                >
                  Скинути фільтри
                </button>
              ) : (
                <button 
                  className="btn btn-primary"
                  onClick={() => setShowForm(true)}
                >
                  Додати перше завдання
                </button>
              )}
            </div>
          )}
        </motion.section>
      </main>
      
      <footer>
        <p>PlanPilot © {new Date().getFullYear()}</p>
      </footer>

      <motion.button 
        className="floating-action-btn"
        onClick={() => setShowForm(true)}
        initial={{ scale: 0, opacity: 0 }}
        animate={{ scale: 1, opacity: 1 }}
        transition={{ delay: 0.5, type: 'spring' }}
        whileHover={{ scale: 1.1 }}
        whileTap={{ scale: 0.9 }}
      >
        <svg width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
          <line x1="12" y1="5" x2="12" y2="19"></line>
          <line x1="5" y1="12" x2="19" y2="12"></line>
        </svg>
      </motion.button>

      <BottomNavbar activePage="tasks" />
    </div>
  );
};

export default TaskApp; 