import React, { useState } from 'react';
import { motion } from 'framer-motion';
import { Task } from '../services/storageService';
import { format } from 'date-fns';
import { uk } from 'date-fns/locale';
import MotivationalQuote from './MotivationalQuote';
import Confetti from './Confetti';

interface TaskItemProps {
  task: Task;
  index: number;
  onStatusChange: (id: string, status: Task['status']) => void;
  onDelete: (id: string) => void;
  onReminderSet?: (id: string, reminderTime: string, enableTelegram: boolean) => void;
  onAddTag?: (id: string, tag: string) => void;
  onRemoveTag?: (id: string, tag: string) => void;
}

const TaskItem: React.FC<TaskItemProps> = ({ 
  task, 
  index, 
  onStatusChange, 
  onDelete, 
  onReminderSet,
  onAddTag,
  onRemoveTag 
}) => {
  const [expanded, setExpanded] = useState(false);
  const [showQuote, setShowQuote] = useState(false);
  const [reminderDate, setReminderDate] = useState('');
  const [reminderTime, setReminderTime] = useState('');
  const [enableTelegramReminder, setEnableTelegramReminder] = useState(false);
  const [showReminderInput, setShowReminderInput] = useState(false);
  const [newTag, setNewTag] = useState('');
  const [showConfetti, setShowConfetti] = useState(false);

  const handleStatusChange = (status: Task['status']) => {
    if (status === 'completed' && task.status !== 'completed') {
      setShowConfetti(true);
    }
    onStatusChange(task.id, status);
  };

  const handleDeleteTask = () => {
    onDelete(task.id);
  };

  const handleSetReminder = () => {
    if (reminderDate && reminderTime && onReminderSet) {
      const reminderDateTime = `${reminderDate}T${reminderTime}:00`;
      onReminderSet(task.id, reminderDateTime, enableTelegramReminder);
      setShowReminderInput(false);
    }
  };

  const handleAddTag = () => {
    if (newTag.trim() && onAddTag) {
      onAddTag(task.id, newTag.trim());
      setNewTag('');
    }
  };

  const handleRemoveTag = (tag: string) => {
    if (onRemoveTag) {
      onRemoveTag(task.id, tag);
    }
  };

  const handleTagKeyPress = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter' && newTag.trim()) {
      handleAddTag();
    }
  };

  return (
    <motion.div 
      className={`task-item ${task.status === 'completed' ? 'completed' : ''} priority-${task.priority}`}
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      exit={{ opacity: 0, y: -20 }}
      transition={{ duration: 0.3, delay: index * 0.05 }}
      layout
    >
      {showConfetti && <Confetti isActive={showConfetti} onComplete={() => setShowConfetti(false)} />}
      
      <div className="task-header" onClick={() => setExpanded(!expanded)}>
        <div className="task-status">
          <button 
            className={`status-indicator ${task.status}`} 
            onClick={(e) => {
              e.stopPropagation();
              const nextStatus = {
                'pending': 'in-progress',
                'in-progress': 'completed',
                'completed': 'pending'
              }[task.status] as Task['status'];
              handleStatusChange(nextStatus);
            }}
          >
            {task.status === 'completed' && '✓'}
          </button>
        </div>
        <div className="task-title-container">
          <h3 className="task-title">{task.title}</h3>
          {task.reminderSet && (
            <span className="task-reminder">
              <i className="bi bi-bell"></i> {task.reminderTime && format(new Date(task.reminderTime), 'dd.MM.yyyy HH:mm', { locale: uk })}
            </span>
          )}
        </div>
        <div className="task-actions">
          <button className="delete-task" onClick={(e) => { e.stopPropagation(); handleDeleteTask(); }}>×</button>
        </div>
      </div>

      {expanded && (
        <div className="task-details">
          <p className="task-description">{task.description || 'Немає опису'}</p>
          
          <div className="task-metadata">
            <span className="task-category">Категорія: {task.category}</span>
            <span className="task-date">Створено: {format(new Date(task.createdAt), 'dd.MM.yyyy', { locale: uk })}</span>
            {task.completedAt && (
              <span className="task-date">Завершено: {format(new Date(task.completedAt), 'dd.MM.yyyy', { locale: uk })}</span>
            )}
          </div>
          
          {/* Теги завдання */}
          {task.tags && task.tags.length > 0 && (
            <div className="task-tags">
              {task.tags.map((tag) => (
                <span key={tag} className="task-tag">
                  {tag}
                  <span 
                    className="remove-tag" 
                    onClick={() => handleRemoveTag(tag)}
                  >×</span>
                </span>
              ))}
            </div>
          )}
          
          {/* Форма додавання тегу */}
          <div className="tag-input">
            <input 
              type="text" 
              value={newTag} 
              onChange={(e) => setNewTag(e.target.value)} 
              placeholder="Додати тег" 
              onKeyPress={handleTagKeyPress}
            />
            <button onClick={handleAddTag}>Додати</button>
          </div>
          
          {/* Секція нагадувань */}
          <div className="task-reminder-section">
            {!showReminderInput ? (
              <div className="reminder-actions">
                {!task.reminderSet ? (
                  <button 
                    className="set-reminder-btn" 
                    onClick={() => setShowReminderInput(true)}
                  >
                    Встановити нагадування
                  </button>
                ) : (
                  <div className="reminder-buttons">
                    <button 
                      className="edit-reminder-btn" 
                      onClick={() => setShowReminderInput(true)}
                    >
                      Змінити нагадування
                    </button>
                    <button 
                      className="cancel-reminder-btn" 
                      onClick={() => onReminderSet && onReminderSet(task.id, '', false)}
                    >
                      Скасувати нагадування
                    </button>
                  </div>
                )}
              </div>
            ) : (
              <div className="reminder-form">
                <div className="reminder-inputs">
                  <input 
                    type="date" 
                    value={reminderDate} 
                    onChange={(e) => setReminderDate(e.target.value)} 
                    min={new Date().toISOString().split('T')[0]}
                  />
                  <input 
                    type="time" 
                    value={reminderTime} 
                    onChange={(e) => setReminderTime(e.target.value)} 
                  />
                </div>
                
                <div className="reminder-telegram">
                  <label>
                    <input 
                      type="checkbox" 
                      checked={enableTelegramReminder} 
                      onChange={(e) => setEnableTelegramReminder(e.target.checked)} 
                    />
                    Нагадування через Telegram
                  </label>
                </div>
                
                <div className="reminder-buttons">
                  <button onClick={handleSetReminder}>Зберегти</button>
                  <button onClick={() => setShowReminderInput(false)}>Скасувати</button>
                </div>
              </div>
            )}
          </div>
          
          {task.status === 'completed' && !showQuote ? (
            <button 
              className="motivational-button" 
              onClick={() => setShowQuote(true)}
            >
              Показати мотиваційну цитату
            </button>
          ) : null}
          
          {showQuote && <MotivationalQuote onClose={() => setShowQuote(false)} />}
        </div>
      )}
    </motion.div>
  );
};

export default TaskItem; 