import React, { useEffect, useState } from 'react';
import { BrowserRouter as Router, Routes, Route, Navigate } from 'react-router-dom';
import './App.css';
import WelcomeScreen from './components/WelcomeScreen';
import FeaturesScreen from './components/FeaturesScreen';
import TaskApp from './components/TaskApp';
import AnalyticsPage from './components/AnalyticsPage';
import ProfilePage from './components/ProfilePage';
import BottomNavbar from './components/BottomNavbar';
import storageService from './services/storageService';
import TelegramWebApp from './components/TelegramWebApp';
import SubscriptionPage from './components/SubscriptionPage';

const App: React.FC = () => {
  const [theme, setTheme] = useState<'light' | 'dark'>(storageService.getThemePreference());
  const [colorScheme, setColorScheme] = useState<string>(storageService.getColorScheme());
  
  useEffect(() => {
    document.body.className = theme === 'dark' ? 'dark-mode' : '';
    
    // Додаємо клас кольорової схеми
    const colorSchemeClass = `color-scheme-${colorScheme}`;
    document.body.classList.add(colorSchemeClass);
    
    return () => {
      document.body.classList.remove(colorSchemeClass);
    };
  }, [theme, colorScheme]);
  
  const handleThemeChange = (newTheme: 'light' | 'dark') => {
    setTheme(newTheme);
    storageService.saveThemePreference(newTheme);
  };
  
  const handleColorSchemeChange = (newScheme: string) => {
    // Видаляємо попередню кольорову схему
    document.body.classList.forEach(className => {
      if (className.startsWith('color-scheme-')) {
        document.body.classList.remove(className);
      }
    });
    
    // Додаємо нову схему
    setColorScheme(newScheme);
    storageService.saveColorScheme(newScheme);
    document.body.classList.add(`color-scheme-${newScheme}`);
  };

  return (
    <Router basename={process.env.PUBLIC_URL}>
      <div className="app-container">
        <Routes>
          <Route path="/" element={<WelcomeScreen />} />
          <Route path="/features" element={<FeaturesScreen />} />
          <Route path="/app" element={<TaskApp />} />
          <Route path="/analytics" element={<AnalyticsPage />} />
          <Route 
            path="/profile" 
            element={
              <ProfilePage 
                theme={theme} 
                onThemeChange={handleThemeChange} 
                colorScheme={colorScheme}
                onColorSchemeChange={handleColorSchemeChange}
              />
            } 
          />
          <Route path="/telegram" element={<TelegramWebApp />} />
          <Route path="/subscription" element={<SubscriptionPage />} />
          <Route path="*" element={<Navigate to="/" replace />} />
        </Routes>
        
        {/* Show bottom navbar on specific pages only */}
        {window.location.hash !== '#/' && window.location.hash !== '#/features' && (
          <BottomNavbar />
        )}
      </div>
    </Router>
  );
};

export default App;
