import React, { useEffect, useState } from 'react';
import { motion } from 'framer-motion';
import BottomNavbar from './BottomNavbar';
import '../styles/TelegramWebApp.css';

const TelegramWebApp: React.FC = () => {
  const [initialized, setInitialized] = useState(false);

  useEffect(() => {
    // Тут буде логіка ініціалізації Telegram Mini App, коли вона буде потрібна
    setInitialized(true);
  }, []);

  return (
    <motion.div 
      className="telegram-webapp-container"
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      exit={{ opacity: 0 }}
    >
      <div className="telegram-header">
        <h1>Telegram інтеграція</h1>
      </div>

      <div className="telegram-content">
        <div className="telegram-info-card">
          <h2>Можливості Telegram інтеграції</h2>
          <ul>
            <li>Отримання нагадувань через Telegram</li>
            <li>Додавання нових завдань через бота</li>
            <li>Перегляд списку завдань через бота</li>
            <li>Швидке відмічання виконаних завдань</li>
          </ul>
        </div>

        <div className="telegram-connect-section">
          <p>Для підключення бота відкрийте вкладку "Профіль" та натисніть кнопку "Підключити Telegram".</p>
        </div>
      </div>

      <BottomNavbar />
    </motion.div>
  );
};

export default TelegramWebApp; 